import React from 'react';
import PropTypes from 'prop-types';
import path from '../../../helpers/path';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './ProductIcon.css';

const bemClasses = getBEMClasses('product-icon');

const ProductIcon = ({
  productManifest,
  iconFileName,
  size = 48,
  customClass
}) => {
  if (productManifest.fetching) {
    return null;
  }

  if (iconFileName == null) {
    console.log(iconFileName);
  }

  if (iconFileName && iconFileName.indexOf('DEFAULT_ICON') > 0) {
    return renderDefaultIcon(size, iconFileName);
  }

  return (
    <img
      alt="product icon"
      height={size + 'px'}
      className={customClass}
      src={path(`/local/product-icons/${iconFileName}`)}
    />
  );
};

const renderDefaultIcon = (size, iconFileName) => {
  return (
    <div
      className={bemClasses(null, 'default')}
      style={{
        width: size,
        height: size,
        MozBorderRadius: size / 2,
        WebkitBorderRadius: size / 2,
        borderRadius: size / 2,
        textAlign: 'center',
        fontSize: size * 0.5,
        lineHeight: `${size}px`
      }}>
      <span>{iconFileName.charAt(0)}</span>
    </div>
  );
};

ProductIcon.propTypes = {
  productManifest: PropTypes.object.isRequired,
  iconFileName: PropTypes.string,
  size: PropTypes.number
};

export default ProductIcon;



