import React from 'react';
import PropTypes from 'prop-types';
import ProductIconContainer from '../common/ProductIcon/ProductIconContainer';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import MenuItemComponent from '../MenuItem/MenuItemComponent';

const baseClass = 'balances-menu';
const baseClasses = getBEMClasses(baseClass);

class BalancesMenuItemComponent extends React.Component {
  handleClick = () => {
    this.props.onItemClick(this.props.position.ProductId);
  };

  render() {
    return (
      <MenuItemComponent
        onItemClick={this.handleClick}
        selected={this.props.selected}
        customClass={baseClass}>
        <div className={`${baseClasses('item-icon')}`}>
          <ProductIconContainer
            iconFileName={this.props.position.iconFileName}
            size={28}
          />
        </div>
        <div className={`${baseClasses('item-details')}`}>
          <div className={`${baseClasses('item-symbol-name')}`}>
            {this.props.position.Product} {this.props.position.ProductFullName}
          </div>
          <div className={`${baseClasses('item-balance-value')}`}>
            {this.props.position.Amount}
            <span className={`${baseClasses('item-value-symbol')}`}>
              {' '}
              {this.props.position.Product}{' '}
            </span>
          </div>
        </div>
      </MenuItemComponent>
    );
  }
}

BalancesMenuItemComponent.propTypes = {
  position: PropTypes.object.isRequired
};

export default BalancesMenuItemComponent;



