import { connect } from 'react-redux';
import { productPositionAssetsSelector } from '../../redux/selectors/productPositionSelectors';
import { selectPositionAndSave } from '../../redux/actions/positionActions';
import BalancesMenuComponent from './BalancesMenuComponent';

const mapStateToProps = state => {
  const positions = productPositionAssetsSelector(state);
  const { position } = state;
  const { selectedProductId } = position;
  return { positions, selectedProductId };
};

const mapDispatchToProps = {
  selectPositionAndSave
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BalancesMenuComponent);



