import { connect } from 'react-redux';
import ProductIconComponent from './ProductIcon';

const mapStateToProps = (state, ownProps) => {
  const productManifest = state.productManifest;
  const iconFileName = ownProps.iconFileName;
  const size = ownProps.size || 48;

  return { productManifest, iconFileName, size };
};

export default connect(mapStateToProps)(ProductIconComponent);



