import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../helpers/cssClassesHelper';

import './MenuComponent.css';

const tradeComponentClasses = getBEMClasses('trade-component');

const baseClass = 'menu-component';

const MenuComponent = props => {
  const { dataTest, children, customClass, header } = props;
  const bemClasses = getBEMClasses(baseClass, customClass);

  return (
    <div className={`${tradeComponentClasses()} ${bemClasses()}`}>
      {header && (
        <div className={`${tradeComponentClasses('header')}`}>
          {props.header}
        </div>
      )}
      <ul className={`${bemClasses('list')}`} data-test={dataTest}>
        {children}
      </ul>
    </div>
  );
};

MenuComponent.propTypes = {
  header: PropTypes.string,
  customClass: PropTypes.string,
  dataTest: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.element),
    PropTypes.element
  ])
};

export default MenuComponent;



