import { createSelector } from 'reselect';
import formatPosition from '../../helpers/positionFormatter';

const productsSelector = state => state.product.products;
const positionsSelector = state => state.position.positions;
const assetsSelector = state => state.productManifest.manifest;

export const fetchingSelector = createSelector(
  [
    state => state.product,
    state => state.position,
    state => state.productManifest
  ],
  (product, position, manifest) => {
    return product.fetching || position.fetching || manifest.fetching;
  }
);

export const productPositionSelector = createSelector(
  [productsSelector, state => state.product.decimalPlaces, positionsSelector],
  (products, decimalPlaces, positions) => {
    return products.map(product => {
      const position = positions[product.ProductId];

      if (!position) {
        return product;
      }

      const positionDecimals = decimalPlaces[product.Product];
      const formattedPosition = formatPosition(position, positionDecimals);

      return {
        ...formattedPosition,
        ...product
      };
    });
  }
);

export const productPositionAssetsSelector = createSelector(
  [productPositionSelector, assetsSelector],
  (products, assets) => {
    return products.map(product => {
      const asset = assets[product.Product] || {
        iconFileName: product.Product + 'DEFAULT_ICON'
      };

      return {
        ...product,
        ...asset
      };
    });
  }
);

export const getProductByIdSelector = createSelector(
  [productPositionAssetsSelector, (state, productId) => productId],
  (products, productId) => {
    return products.find(item => item.ProductId === productId);
  }
);

export const fundedPositionsSelector = createSelector(
  productPositionAssetsSelector,
  positions => {
    // productPositionsSelector replaces Amount 0 with '-'
    return positions.filter(position => position.Amount !== '-');
  }
);

export const unfundedPositionsSelector = createSelector(
  productPositionAssetsSelector,
  positions => {
    // productPositionsSelector replaces Amount 0 with '-'
    return positions.filter(position => position.Amount === '-');
  }
);



